export default {
    props:{
        item:Object,
        index:Number
    },
    methods:{
        clickEven(){
            switch (this.item.clickType){
                case "navigateTo":
                    if(this.item.url){
                         uni.navigateTo({url: this.item.url});
                    }
                    break;
                case "switchTab":
                    if(this.item.url){
                         uni.switchTab({url: this.item.url});
                    }
                    break;
                case "clear":
                   uni.showModal({
                       title:"提示",
                       content:"是否清除缓存？",
                       confirmText:"立即清除",
                       success: (res) => {
                           if(res.confirm){
                               // 清除缓存
                               uni.clearStorage();
                               uni.showToast({
                                   title:"清除成功！",
                                   duration:1000
                               });
                           }
                       }
                   });
                    break;
            }
        }
    }
}
